
import { ref } from "@vue/reactivity"
export default {
  props: {
    nowInfo: {
      type: Array,
      required: true,
    },
  },
  setup(props: any) {
    const nowData = ref({
      nowTemp: props.nowInfo.nowTemp,
      nowSky: props.nowInfo.nowSky,
    })

    return { nowData }
  },
}
