
import { Ref, ref } from "vue"

type SetUpTypes = {
  location: Ref<string>
}

export default {
  props: {
    locationName: {
      type: String,
      required: true,
    },
  },
  // #TODO : #2. any타입 변경
  setup(props: any): SetUpTypes {
    const location = ref(props.locationName)

    return { location }
  },
}
